}
.label{
    padding: 4px 8px;
    background-color: rgba(107, 116, 240, 0.2);
    border-radius: 4px;
    font-weight: 600;
    font-size: 13px;
    color: #1F75E1;
    border: 1px solid #1F75E1;
}
.text-title{
    font-weight: 700;
    font-size: 15px;
    margin-top: 16px;
}
.text-button{
    font-weight: 600;
    font-size: 15px;
}
.btn-block{
    border: 2px solid #163C80;
    border-radius: 2px;
    padding: 12px 20px;
    text-align: center;
    color: white;
}
.btn-block:hover{
    background-color:#163C80 ;
}
.btn-block:hover>.text-primary{
    color: white;
}
.btn-primary{
    background-color: var(--primary);
    border: none;
    border-radius: 2px;
    text-align: center;
    color: var(--white);
}
.btn-primary:hover{
    background-color: var(--white);
    border: 1px solid var(--primary);
    color: var(--primary);
}
.btn-outline-primary{
    border: 2px solid #163C80;
    border-radius: 2px;
    text-align: center;
    background-color: #fff;
    color: var(--primary);
}
.btn-outline-primary:hover{
    background-color: var(--primary);
    color: white;

}
.btn-block-white{
    background-color: transparent;
    border-radius: 2px;
    border: 1px solid white;
    color: white;
    font-size: 14px;
    font-weight: 600;
    padding: 8px 16px;
    margin: 16px 0;
    cursor: pointer;
}
.btn-block-white:hover{
    background-color: #fff;
    color: var(--primary);
}
.btn-white{
    background-color: white;
    border-radius: 2px;
    border: 1px solid white;
    color: var(--primary);
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
}
.btn-white:hover{
    background-color: var(--primary);
    color: var(--white);
}
.btn-link{
    border: none;
    background-color: transparent;
    /* padding: 6px 8px; */
    color: var(--black);
    cursor: pointer;
}
.border-active{
    border-bottom: 1px solid #000;
}
.inline{
    display: inline-block;
    gap: 10px;
}
/* display */
.display-block{
    display: block;
}
.display-none{
    display: none;
}
