    border: 2px solid #163C80;
    border-radius: 2px;
    padding: 10px 20px;
    text-align: center;
    margin: 10px 30px;
    cursor: pointer;
}
.phone{
    border: 2px solid #163C80;
    border-radius: 2px;
    padding: 10px 20px;
    text-align: center;
    margin: 10px 34px;
    cursor: pointer;
}
/* Slider */
.slide-container{
    position: relative;
    max-width: 500px;
    margin: auto;
}
.slider{
    display: none;
}
.slider>img{
    vertical-align: middle;
}
.prev,.next{
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    color: white;
    font-weight: 600;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
}
.next{
    right: 0;
    border-radius: 3px 0 0 3px;
}
.prev:hover, .next:hover{
    color: black;
    background-color: #f1f1f1;
}
.dot{
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: 0.6s ease;
}
.active .dot:hover{
    background-color: #717171;
}
.fade{
    -webkit-animation-name:fade ;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
}
@-webkit-keyframes fade{
    from{opacity: .4;}
    to{opacity: 1;}
}
@keyframes fade{
    from{opacity: .4;}
    to{opacity: 1;}
}
@media only screen and(max-width : 300px) {
    .prev,.next{
        font-size: 11px;
    }
}
